header{
  text-align: center;
  font-size: 24px;
  font-weight: 600;
  margin-bottom: 20px;
  margin-top: 20px;
}
.settings {
  text-align: center;
}
.actions {
  margin: 0 auto;
  width: 300px;
  display: flex;
  align-items: center;
  flex-direction: column;
  flex-wrap: nowrap;
}
.actions button {
  align-items: center;
  background-color: #fff;
  border-radius: 24px;
  border-style: none;
  color: #333;
  font-weight: 600;
  cursor: pointer;
  font-size: 14px;
  height: 48px;
  transition: box-shadow 280ms cubic-bezier(.4, 0, .2, 1),opacity 15ms linear 30ms,transform 270ms cubic-bezier(0, 0, .2, 1) 0ms;
  user-select: none;
  width: 230px;
  margin-bottom: 20px;
}

.actions button:hover {
  background: #F6F9FE;
  color: orangered;
}

.actions button.selected {
  outline: none;
  border: 2px solid orange;
}

.settings input, .circleTokenInput input {
  color: #333;
  font-size: 1.2rem;
	margin: 0 auto;
  padding: 12px 12px 8px;
  border-radius: 8px;
  background-color: rgb(255, 255, 255);
  border: none;
  display: block;
  border-bottom: 0.3rem solid transparent;
  transition: all 0.3s;
  line-height: 24px;
  height: 24px;
  outline-color: rgba(141, 126, 126, 0.5);
  outline-offset: -4px;

}
.settings input :focus {
  outline-color: red;
  box-shadow: none;
}

.triggerContainer{
  flex-direction: column;
  align-items: center;
  display: flex;
  margin-top: 40px;
}
.triggerMsg{
  display: flex;
  flex-direction: column;
  align-items: center;
}
.trigger {
  cursor: pointer;
  height: 7em;
  width: 7em;
  border-radius: 50%;
  border-color: transparent;
  border-width: 5px;
  border-style: inset;
  background-color: rgb(216, 65, 65);
  color: white;
  font-size: 22px;
  font-weight: 600;
  transition: all 0.3s ease-in;
}
.trigger:hover {
  color: orangered;
  background-color: rgb(155, 46, 46);
}
.vanish {
  opacity: 0;
  pointer-events: none;
}
.appear {
  opacity: 1;
}

.footer {
  background: black;
  position: fixed;
  bottom: 0;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 24px;
  font-size: 14px;
}
.footer img {
  filter: invert(1);
  height: 18px;
  margin-left: 8px;
  cursor: pointer;
}

.circleTokenInput{
  margin-bottom: 12px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.circleTokenInput input {
  margin-top: 4px;
  width: 500px;
}

.tip{
  display: flex;
  justify-content: center;
  font-size: 12px;
}

.fredrick{
  transform: rotate(180deg);
  position: absolute;
  top: -10px;
}